@import "../../Assets/styles/variables";

.singleProject {
    background-color: $primary-color;
    box-shadow: 4px 4px 8px rgba(36, 3, 3, 0.2);
    width: 304px;
    height: 360px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1.4rem 2rem;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.projectContent {
    color: $text-color;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;

    &-name {
        font-style: normal;
        font-weight: 600;
        font-size: 1.525rem;
        line-height: 110%;
        text-align: center;
        color: $text-color
    }

    &-image {
        width: 100%;
        height: 60%;
        transition: opacity 0.7s 0.3s;
    }

    &-image {
        &:hover {
            opacity: 0;
        }
    }

}

.project {

    &--showcaseBtn {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        & > a {
            cursor: pointer;
            text-decoration: none;
            transition: transform 0.5s 0.3s;
        }

        &-iconBtn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40;
            height: 40;
            border-radius: 100%;
            padding: 8px;
            border: 2px solid $text-color;
            color: $text-color;
            transition: all 0.2s;
        
            &:hover {
                background-color: $secondary-color;
                color: $primary-color;
                transform: scale(1.1);
                border: 2px solid $secondary-color
            }
        }

        &-icon {
            font-size: 1.1rem;
            transition: all 0.2s;
            &:hover {
        
            }
        }
    }

    &--desc {
        background-color: $secondary-color;
        color: $text-color;
    }

    &--lang {
        background-color: $secondary-color;
        color: $text-color;
    }
}

.singleProject:hover .project--showcaseBtn a:nth-child(2) {
    transform: translateX(-140px) scale(1.1) !important;
}

.singleProject .project--desc{
    position: absolute;
    width: 95%;
    height: 160px;
    left: 0;
    top: 0;
    transform: translate(-110%,40%);
    padding: 15px;
    border-radius: 0 20px 20px 0;
    transition: transform 0.9s;
    line-height: 110%;
    display: flex;
    // align-items: center;
    justify-content: center;
    font-family: var(--primaryFont);
}

.singleProject:hover .project--desc{
    transform: translate(-2%,40%);
}
  
.singleProject .project--lang{
    position: absolute;
    bottom: 20px;
    right: 0;
    width: 140px;
    font-size: 0.8rem;
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    flex-direction: column;
    border-radius: 10px 0 0 10px;
    transform: translateX(100%);
    transition: transform 0.5s 0.3s;
    padding: 0.825rem;
}
  
.singleProject:hover .project--lang{
    transform: translateX(0);
}

.project--lang > span {
    font-family: var(--primaryFont);
    font-weight: 500;
    word-break: break-word; 
    line-height: 100%;
}

.project--desc {
    overflow: scroll;
}
